import './sass/main.scss';

const refs = {
    ClockFace: document.querySelector('#timer-1'),
    ClockFaceDays: document.querySelector('[data-value="days"]'),
    ClockFaceHours: document.querySelector('[data-value="hours"]'),
    ClockFaceMinuts: document.querySelector('[data-value="mins"]'),
    ClockFaceSeconds: document.querySelector('[data-value="secs"]'),
};

class CountdownTimer {
    constructor({selector, targetDate, onTick}) {
        this.selector = selector;
        this.targetDate = targetDate;
        this.onTick = onTick;
    }

    start() {
        setInterval(() => {
            const currentTime = Date.now();
            const deltaTime = this.targetDate - currentTime;
            const time = getTimeComponents(deltaTime);
            this.onTick(time);
        }, 1000);
    };
}

const timer = new CountdownTimer({
    // –Ω–∏—Ö—Ä–µ–Ω–∞ –Ω–µ –ø–æ–Ω—è–ª –∫–∞–∫ –º–æ–∂–Ω–æ –ø–æ–≤–µ—Å–∏—Ç—å –∑–Ω–∞—á–µ–Ω–∏—è —Ç–∞–π–º–µ—Ä–∞ –Ω–∞ –≤–µ—Å—å div –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä
    // selector: '#timer-1', —á–µ—Ä–µ–∑ —Ñ—É–Ω–∫—Ü–∏—é?
    targetDate: new Date('Jul 17, 2021'),
    onTick: updateClockface,
    
});

timer.start();

function updateClockface({days, hours, mins, secs}) {
    refs.ClockFaceDays.textContent = `${days}`;
    refs.ClockFaceHours.textContent = `${hours}`;
    refs.ClockFaceMinuts.textContent = `${mins}`;
    refs.ClockFaceSeconds.textContent = `${secs}`;
};

function pad(value) {
    return String(value).padStart(2, '0');
};

function getTimeComponents(time) {
    /*
 * –û—Å—Ç–∞–≤—à–∏–µ—Å—è –¥–Ω–∏: –¥–µ–ª–∏–º –∑–Ω–∞—á–µ–Ω–∏–µ UTC –Ω–∞ 1000 * 60 * 60 * 24, –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ
 * –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ –æ–¥–Ω–æ–º –¥–Ω–µ (–º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã * —Å–µ–∫—É–Ω–¥—ã * –º–∏–Ω—É—Ç—ã * —á–∞—Å—ã)
 */
    const days = pad(Math.floor(time / (1000 * 60 * 60 * 24)));
    /*
 * –û—Å—Ç–∞–≤—à–∏–µ—Å—è —á–∞—Å—ã: –ø–æ–ª—É—á–∞–µ–º –æ—Å—Ç–∞—Ç–æ–∫ –æ—Ç –ø—Ä–µ–¥—ã–¥—É—â–µ–≥–æ —Ä–∞—Å—á–µ—Ç–∞ —Å –ø–æ–º–æ—â—å—é –æ–ø–µ—Ä–∞—Ç–æ—Ä–∞
 * –æ—Å—Ç–∞—Ç–∫–∞ % –∏ –¥–µ–ª–∏–º –µ–≥–æ –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ –æ–¥–Ω–æ–º —á–∞—Å–µ
 * (1000 * 60 * 60 = –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã * –º–∏–Ω—É—Ç—ã * —Å–µ–∫—É–Ω–¥—ã)
 */
    const hours = pad(Math.floor((time % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)));
    /*
 * –û—Å—Ç–∞–≤—à–∏–µ—Å—è –º–∏–Ω—É—Ç—ã: –ø–æ–ª—É—á–∞–µ–º –æ—Å—Ç–∞–≤—à–∏–µ—Å—è –º–∏–Ω—É—Ç—ã –∏ –¥–µ–ª–∏–º –∏—Ö –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ
 * –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ –æ–¥–Ω–æ–π –º–∏–Ω—É—Ç–µ (1000 * 60 = –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥—ã * —Å–µ–∫—É–Ω–¥—ã)
 */
    const mins = pad(Math.floor((time % (1000 * 60 * 60)) / (1000 * 60)));
    /*
 * –û—Å—Ç–∞–≤—à–∏–µ—Å—è —Å–µ–∫—É–Ω–¥—ã: –ø–æ–ª—É—á–∞–µ–º –æ—Å—Ç–∞–≤—à–∏–µ—Å—è —Å–µ–∫—É–Ω–¥—ã –∏ –¥–µ–ª–∏–º –∏—Ö –Ω–∞ –∫–æ–ª–∏—á–µ—Å—Ç–≤–æ
 * –º–∏–ª–ª–∏—Å–µ–∫—É–Ω–¥ –≤ –æ–¥–Ω–æ–π —Å–µ–∫—É–Ω–¥–µ (1000)
 */
    const secs = pad(Math.floor((time % (1000 * 60)) / 1000));

    return { days, hours, mins, secs };
};
